// Global Styling
html body {
    -webkit-font-smoothing: antialiased;
    font-family: 'Lato', 'Helvetica', 'Arial', sans-serif !important;
    position: relative;

    &.overflow-hidden {
        overflow: hidden;
    }
}

h1 {
  // color: green;
  // font-size: 20px;
}

h2 {
    font-size: $big;
    font-weight: $extra-bold;
}

h3 {
    font-size: $medium;
    font-weight: $normal;
    line-height: 30px;
}

h4 {
    font-size: $medium;
    font-weight:$bold;
    line-height:18px;
}

p {
    font-size:$small;
    font-weight:$bold;
    line-height:21px;
}

h2,
h3,
h4,
p,
span {
    color: $grey;
}

