// Background Wrappers
.section-wpr {

    &.dblue {
        background-color: $dblue;
        border-bottom: 6px solid $light-dblue;

    }

    &.pink {
        background-color:$pink;
        border-bottom: 6px solid $light-pink;

    }

    &.off-white {
        background-color: $off-white;
        border-bottom: 6px solid $white;

    }
}
